@media (max-width: 79.5em) {
  .help-text {
    font-size: 1.5rem;
  }

  .help-title {
    font-size: 3rem;
  }
}
@media (max-width: 56.3em) {
  .bug-modal {
    padding: 2rem;
    width: 100vw;
    height: 100vh;

    overflow-y: scroll;
    padding: 5 3rem;
  }

  .bug-modal-container {
    height: 90%;
    overflow-y: auto;
    align-self: center;
  }
  .new-bug-form {
    display: flex;
    flex-direction: column;

    padding: 1.2rem;
  }

  .help-modal {
    width: 100vw;
    height: 100vh;
  }

  /* .bugs-user-container {
    overflow-y: scroll;
  } */

  .section-main {
    height: 80vh;

    overflow-y: auto;
  }
}

@media (max-width: 52.43em) {
  html {
    font: 52.2%;
  }

  .page-content-container {
    display: flex;
    flex-direction: column;
  }
  .main-aside {
    width: 100%;
    height: auto;
  }

  .aside-nav-list {
    /* flex-direction: row; */
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    align-items: center;
    justify-items: center;
    text-align: center;

    /* gap: 1.2rem; */
  }

  .project-selected-view {
    display: none;
  }

  .btn-projects-delete {
    grid-column: -2 / -1;
  }
  .add-project-container {
    grid-column: span 3;
    width: 100%;
  }

  .logo {
    display: none;
  }

  .header {
    height: 6rem;
  }
}

@media (max-width: 43em) {
  html {
    font-size: 50%;
  }

  .btn-bug--delete {
    font-size: 1.4rem;
  }
}

@media (max-width: 33em) {
  .input-teamcode {
    width: 12rem;
  }

  .content-heading {
    margin-bottom: 1rem;
  }

  .btn-projects-delete,
  .btn-projects-back {
    width: 10rem;
  }

  .login-form-container {
    text-align: center;
    height: 100vh;
    width: 100vw;
  }
}

@media (max-width: 26.7em) {
  .input-search {
    width: 10rem;
  }
}
